main {
	background: linear-gradient(to bottom, white 56px, $light-gray 56px);
	padding-top: 56px;
	height: 100vh;
	overflow-y: scroll;
	@media only screen and (max-width: 800px ) {
		padding-bottom: 56px; } }

///////////////////////
// dashboard
///////////////////////
.dashboard:not(.gnb-item) {
	@extend %clearfix; }

.dashboard-section {
	@include relative;
	width: 50%;
	float: left;
	border: 2px solid $light-gray;
	background-color: white;

	&.summary {
		width: 100%; }
	&:after {
		content: '';
		display: block;
		width: 100%;
		padding-bottom: 100%; }

	.dashboard-contents {
		@include absolute(top 0 left 0);
		width: 100%;
		text-align: center;
		padding-top: 5%;
		.dashboard-title {
			margin-bottom: 1rem; } } }

.readable-hidden {
	overflow: hidden;
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0; }

@media only screen and (min-width: 500px ) and (max-width: 799px ) {
	.dashboard-section {
		width: 33.3%; }
	.dashboard-section.summary {
		width: 66.6%; } }

@media only screen and (min-width: 800px ) and (max-width: 1259px) {
	.dashboard-section {
		width: 25%; }
	.dashboard-section.summary {
		width: 50%; } }

@include width-from('1260px') {
	.dashboard-section {
		width: 20%; }
	.dashboard-section.summary {
		width: 40%; } }

////////////////////
//section .daily
////////////////////
.daily ul {
	@extend %clearfix; }

.daily-item {
	@include relative;
	float: left;
	width: 25%;
	&:first-child {
		width: 100%; } }

@include width-from(1200px) {
	.daily-item {
		margin: {
			top: 10%;
			bottom: 15%; } } }

//////////////////////////
// svg ring graph
///////////////////////////
svg.ring-graph {
		@include size(100px, 100px); }

svg .circle {
	stroke: $light-gray;
	stroke-width: 0.5em;
	stroke-linecap: round;
	transition: stroke-dashoffset 0.8s ease-out; }

///////////////////////
// section .heart-rate
///////////////////////

@keyframes heartbeat {
	0%, 100% {
		transform: scale(0.9);
		fill: rgba(#f44, 0.5); }
	50% {
		transform: scale(1.3);
		fill: rgba(#E86363, 0.5); } }

.heart-icon {
 	fill: #E86363;
 	transform-origin: center;
 	&.big {
 		fill: rgba(#E86363, 0.3);
 		animation: heartbeat 1.5s infinite ease; } }

.heart-rate .svg-container svg {
	width: 160px;
	margin: {
		left: auto;
		right: auto; } }

///////////////////////
// util-btns
///////////////////////
.utils {
	@extend %clearfix;
	@include fixed(bottom 0 left 0);
	width: 100%;
	background: $light-gray;
	@include width-from('1260px') {
		width: auto;
		@include absolute(top 0 right 0 left initial bottom initial);
		background: none; } }

.util-btns {
	float: right;
	&> button {
		@include ir;
		width: 4rem;
		padding: 1em;
		border: none; } }

@each $icon in add, setting {
	.util-btns .#{$icon}-btn {
		background: url(../img/icon/#{$icon}.svg) no-repeat center;
		background-size: 1.5rem; } }



////////////////////////
.svg-container {
	width: 60%;
	height: 60%;
	margin-left: auto;
	margin-right: auto;
	@include relative; }
.svg-container img {
	width: 100%; }
.drink {
	background: blue;
	position: absolute;
	width: 100%;
	height: 0; }
.glass {
	border: 5px solid black;
	border-top-color: transparent; }
.glass:before {
	content: '';
	display: block;
	width: 100%;
	height: 100px; }


.test {
	@include absolute(top 50% left 50%);
	transform: translate(-50%, -50%); }
