//////////////////////////////////////////
// gnb
//////////////////////////////////////////
.gnb {
	@extend %clearfix;
	@include position(fixed);
	z-index: 100;
	background: white;
	width: 100%;
	max-width: 970px;
	text-align: center;
	.gnb-item {
		@include size(20%);
		@include ir;
		@include relative;
		padding: 1rem;
		float: left;
		transition: all 0.2s ease-out;
		color: black;
		@include width-from('970px') {
			text-indent: 6rem; }
		&:after {
			content: '';
			@include absolute(left 0 bottom 0 right 0 top 100%);
			background: $fit-green;
			transition: all 0.2s ease; }
		//상태디자인 필요 hover,focus, active
		&:hover, &:focus, &:active {
			background-size: 2rem;
			&:after {
				top: 90%; } } } }

@each $icon in challenge, dashboard, friend, myInfo, notification {
	.gnb-item.#{$icon} {
		background: url(../img/icon/#{$icon}.svg) no-repeat center;
		background-size: 1.5rem; } }
